<template>
  <div class="page">
    <div class="page__header">
      <h1>اطلاعات کاربران</h1>
      <div class="page__create" @click="addUser()">
        <addSvg />
        <span>افزودن کاربر </span>
      </div>
    </div>
    <div class="mt-6">
      <v-data-table
        :headers="headersUsers"
        :items="profiles"
        :items-per-page="5"
        class="elevation-1"
        :loading="loading"
      >
        <template v-slot:item.phoneNumber="{ item }">
          <div style="direction: initial">
            {{ item.phoneNumber }}
          </div>
        </template>
        <template v-slot:item.createdAt="{ item }">
          {{ new Date(item.createdAt).toLocaleTimeString("fa-IR") }}
          {{ new Date(item.createdAt).toLocaleDateString("fa-IR") }}
        </template>
        <template v-slot:item.active="{ item }">
          <v-chip small color="success" v-if="item.active"> فعال </v-chip>
          <v-chip color="error" small v-else> غیر فعال </v-chip>
        </template>

        <template v-slot:item.operation="{ item }">
          <v-btn
            color="primary"
            x-small
            outlined
            class="ml-2"
            @click="editClicked(item)"
            >ویرایش</v-btn
          >
          <v-btn
            color="blue"
            x-small
            outlined
            class="ml-2"
            @click="$router.push(`site-users/${item._id}`)"
            >جزییات</v-btn
          >

          <v-btn color="error" x-small outlined @click="deleteClicked(item)"
            >حذف</v-btn
          >
        </template>
      </v-data-table>
    </div>

    <base-modal
      :show="showFormModal"
      @close="showFormModal = false"
      :maxWidth="700"
      :title="`${mode === 'add' ? 'افزودن' : 'ویرایش'} کاربر`"
    >
      <div>
        <ValidationObserver ref="form" v-slot="{ handleSubmit }">
          <form class="form" @submit="handleSubmit(add)">
            <v-row>
              <v-col cols="12" sm="12" md="6">
                <ValidationProvider
                  name="شماره موبایل"
                  rules="required|isMobile2"
                  v-slot="{ errors }"
                >
                  <v-text-field
                    label="شماره موبایل"
                    variant="underlined"
                    v-model="model.phoneNumber"
                    :error-messages="errors[0]"
                    hint="+98151234567 مثال"
                  ></v-text-field>
                </ValidationProvider>
              </v-col>
              <v-col cols="12" sm="12" md="6">
                <ValidationProvider
                  name="رمز عبور"
                  rules=""
                  v-slot="{ errors }"
                >
                  <v-text-field
                    label="رمز عبور"
                    variant="underlined"
                    v-model="model.password"
                    :error-messages="errors[0]"
                  ></v-text-field>
                </ValidationProvider>
              </v-col>
              <v-col cols="12" sm="12" md="6">
                <!-- <ValidationProvider
                  name="ایمیل"
                  rules="required|email"
                  v-slot="{ errors }"
                >
                  <v-text-field
                    label="ایمیل"
                    variant="underlined"
                    v-model="model.email"
                    :error-messages="errors[0]"
                    :disabled="mode == 'edit'"
                  ></v-text-field> -->
                <v-select
                  :items="[
                    { title: 'فعال', value: true },
                    { title: 'غیر فعال', value: false },
                  ]"
                  v-model="model.active"
                  item-text="title"
                  item-value="value"
                ></v-select>
              </v-col>
              <!-- <v-col cols="12" sm="12" md="6">
                <ValidationProvider
                  name="آدرس"
                  rules="required"
                  v-slot="{ errors }"
                >
                  <v-text-field
                    label="آدرس"
                    variant="underlined"
                    v-model="model.address"
                    :error-messages="errors[0]"
                  ></v-text-field>
                </ValidationProvider>
              </v-col> -->

              <v-col cols="12" sm="12">
                <v-btn color="primary" block @click="submit()">ثبت</v-btn>
              </v-col>
            </v-row>
          </form>
        </ValidationObserver>
      </div>
    </base-modal>

    <confirmModal
      :show="showDeleteModal"
      title="حذف کاربر"
      message="آیا می خواهید کاربر را حذف کنید"
      @cancel="showDeleteModal = false"
      @close="showDeleteModal = false"
      @confirm="deleteUserConfirm()"
    ></confirmModal>
  </div>
</template>
 
 <script>
import axios from "axios";
import addSvg from "@/assets/svg/ic_add-circle.svg";
import BaseModal from "@/components/Dashboard/modal/modal.component.vue";
import confirmModal from "@/components/Dashboard/modal/confirm.modal.vue";
import { ValidationObserver, ValidationProvider } from "vee-validate";

export default {
  name: "Profile",
  components: {
    addSvg,
    BaseModal,
    confirmModal,
    ValidationObserver,
    ValidationProvider,
  },
  data() {
    return {
      loading: false,
      showFormModal: false,
      showDeleteModal: false,
      mode: "add",
      selected: null,
      model: {
        // email: "",
        // address: "",
        // name: "",
        // lastName: "",
        phoneNumber: "",
        password: "",
        active: true,
      },
      headers: [
        {
          text: "نام",
          align: "center",
          sortable: false,
          value: "name",
        },
        {
          text: "نام خانوادگی",
          align: "center",
          sortable: false,
          value: "lastName",
        },
        {
          text: "ایمیل",
          align: "center",
          sortable: false,
          value: "email",
        },
        {
          text: "آدرس",
          align: "center",
          sortable: false,
          value: "address",
        },
        {
          text: "عملیات",
          align: "start",
          sortable: false,
          value: "operation",
        },
      ],
      headersUsers: [
        {
          text: "شماره موبایل",
          align: "center",
          sortable: false,
          value: "phoneNumber",
        },
        {
          text: "تاریخ ایجاد",
          align: "center",
          sortable: false,
          value: "createdAt",
        },
        {
          text: "وضعیت",
          align: "center",
          sortable: false,
          value: "active",
        },
        {
          text: "عملیات",
          align: "center",
          sortable: false,
          value: "operation",
        },
      ],
      profiles: [],
    };
  },
  mounted() {
    // console.log('aaa');
    this.getProfile();
  },
  watch: {
    showFormModal(val) {
      if (!val)
        this.model = {
          phoneNumber: "",
          password: "",
          active: true,
        };
    },
  },
  methods: {
    async getProfile() {
      this.loading = true;
      try {
        const { data } = await axios({
          method: "GET",
          url: "/users",
          headers: {
            Authorization: "Bearer " + this.$cookies.get('admin-token'),
          },
        });
        this.profiles = data;
      } catch {
        this.$toast.error("خطایی رخ داد");
      } finally {
        this.loading = false;
      }
    },
    editClicked(item) {
      this.showFormModal = true;
      this.mode = "edit";
      this.selected = JSON.parse(JSON.stringify(item));
      this.model = JSON.parse(JSON.stringify(item));
    },
    deleteClicked(item) {
      this.selected = JSON.parse(JSON.stringify(item));
      this.showDeleteModal = true;
    },
    addUser() {
      this.showFormModal = true;
      this.mode = "add";
    },
    async deleteUserConfirm() {
      try {
        await axios({
          method: "DELETE",
          url: `/profile/admin/${this.selected._id}`,
          headers: {
            Authorization: "Bearer " + this.$cookies.get('admin-token'),
          },
        });
        this.showFormModal = false
        this.getProfile();
      } catch {
        this.$toast.error("خطایی رخ داد");
      } finally {
        this.loading = false;
      }
    },
    async submit() {
      if (!(await this.$refs.form.validate())) {
        return;
      }

      const dataToSend = {
        phoneNumber: this.model.phoneNumber,
        password: this.model.password,
        enabled: this.model.active
      };

      // if (dataToSend.lastName.length < 3 || dataToSend.name.length < 3) {
      //   this.$toast.error("نام  و نام خانوادگی باید بیشتر از 3 کاراکتر باشد");
      //   return;
      // }

      // const reg = new RegExp(
      //   /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/
      // );
      // if (!reg.test("" + dataToSend.email)) {
      //   this.$toast.error("ایمیل معتبر نمی باشد");
      //   return;
      // }

      if (dataToSend.password?.length < 8) {
        this.$toast.error("  رمز عبور باید بیشتر از 8 کاراکتر باشد");
        return;
      }
      else if(!dataToSend.password) {
        delete dataToSend.password
      }

      // if (this.mode == "edit") delete dataToSend.email;

      console.log(dataToSend);

      try {
        await axios({
          method: this.mode == "add" ? "POST" : "PATCH",
          url: `/users/${this.mode === "edit" ? this.selected._id : ""}`,
          headers: {
            Authorization: "Bearer " + this.$cookies.get('admin-token'),
          },
          data: dataToSend,
        });
        this.showFormModal = false

        this.getProfile();
      } catch {
        this.$toast.error("خطایی رخ داد");
      } finally {
        this.loading = false;
      }
    },
    async putProfile() {
      this.loading = true;
      try {
        await axios({
          method: "PATCH",
          url: "/profile",
          headers: {
            Authorization: "Bearer " + this.$cookies.get('admin-token'),
          },
          data: this.profile,
        });
      } catch {
        this.$toast.error("خطایی رخ داد");
      } finally {
        this.loading = false;
      }
    },
  },
};
</script>
 
 <style lang="scss" scoped>
.page {
  &__header {
    display: flex;
    justify-content: space-between;
  }
  &__create {
    display: flex;
    align-items: center;
    color: var(--primary-lighter);
    cursor: pointer;
    &:hover {
      transform: scale(1.05);
      transition: all 0.5s;
    }
  }
}
</style>